import { T_ConversionFormulas, T_UnitConversionAllUnitsMap } from "shared/types/unitConverter";
import { T_FavoriteCodes } from "shared/components/unitConverter/types";
import { toNumber, round } from "shared/helpers/number";

const unitsMap: T_UnitConversionAllUnitsMap = {
  K: {
    name: "Kelvin",
    symbol: "K"
  },
  C: {
    name: "Celsius",
    symbol: "°C"
  },
  F: {
    name: "Fahrenheit",
    symbol: "°F"
  },
  R: {
    name: "Rankine",
    symbol: "°R"
  }
  // RO: {
  //   name: "Rømer",
  //   symbol: "°Rø"
  // },
  // RE: {
  //   name: "Réaumur",
  //   symbol: "°Ré"
  // },
  // N: {
  //   name: "Newton",
  //   symbol: "°N"
  // },
  // D: {
  //   name: "Delisle",
  //   symbol: "°D"
  // },
  // L: {
  //   name: "Leiden",
  //   symbol: "°L"
  // },
  // W: {
  //   name: "Wedgwood",
  //   symbol: "°W"
  // }
};

type Kelvin = number;
type Fahrenheit = number;
type Celcius = number;

const baseUnit = "K";
const conversionFormulas: T_ConversionFormulas = {
  baseUnitCode: baseUnit,
  fromBaseTo: {
    K: (K: Kelvin) => toNumber(K),
    C: (K: Kelvin) => round(toNumber(K) - 273.15),
    F: (K: Kelvin) => round(((toNumber(K) - 273.15) * 9) / 5) + 32,
    R: (K: Kelvin) => round((toNumber(K) * 9) / 5)
  },
  toBaseFrom: {
    K: (K: Kelvin) => toNumber(K),
    C: (C: Celcius) => round(toNumber(C) + 273.15),
    F: (F: Fahrenheit) => round(((toNumber(F) - 32) * 5) / 9) + 273.15,
    R: (R: Kelvin) => round((toNumber(R) * 5) / 9)
  }
};

const defaults = {
  inputCode: "C",
  targetCode: "F",
  favoriteCodes: Object.keys(unitsMap) as T_FavoriteCodes
};

const temperatureConstants = {
  unitsMap,
  baseUnit,
  defaults,
  conversionFormulas
}

export default temperatureConstants;
